import React, { Component } from 'react';
import {
  Grid, Jumbotron, Button, ButtonToolbar,
  Row, Col, FormGroup, ControlLabel, FormControl, Form, HelpBlock
} from 'react-bootstrap';
import { MSG_BEGIN, MSG_END, encrypt_v3, decrypt_v3 } from './hootie-v3';
import { CountdownTimer } from './CountdownTimer';

const HootieForm = React.createClass({
  DEFAULT_TIMEOUT: 300000,

  getInitialState() {
    return {
      key: '',
      keyInvalid: false,
      confirm: '',
      confirmInvalid: false,
      input: '',
      inputMsg: null,
      output: '',
      timeout: this.DEFAULT_TIMEOUT
    };
  },

  handleKeyChange(e) {
    this.setState({
      key: e.target.value,
      keyInvalid: false,
      confirmInvalid: false,
      timeout: this.DEFAULT_TIMEOUT
    });
  },

  handleConfirmChange(e) {
    this.setState({
      confirm: e.target.value,
      confirmInvalid: false,
      keyInvalid: false,
      timeout: this.DEFAULT_TIMEOUT
    });
  },

  handleInputChange(e) {
    this.setState({
      input: e.target.value,
      inputMsg: null,
      timeout: this.DEFAULT_TIMEOUT
    });
  },

  handleOutputChange(e) {
    this.setState({
      output: e.target.value,
      timeout: this.DEFAULT_TIMEOUT
    });
  },

  handleClearKeys(e) {
    this.setState({
      key: '',
      keyInvalid: false,
      confirm: '',
      confirmInvalid: false
    });
  },

  handleClearAll(e) {
    this.setState({
      key: '',
      keyInvalid: false,
      confirm: '',
      confirmInvalid: false,
      input: '',
      inputMsg: null,
      output: '',
      timeout: 0
    });
  },

  handleCopyUp(e) {
    this.setState({ input: this.state.output });
  },

  // Called when the component is mounted (once) - and can wire up keypress listener
  componentDidMount() {
    //document.addEventListener('keypress', function() {
    // search for how to listen for specific key presses
    //this.handleEncrypt()
    //})
  },

  handleDecrypt(e) {
    if (this.state.key.length === 0) {
      this.setState({ keyInvalid: true });
    } else {
      const msg = this.state.input;

      // Check for start marker
      const startIndex = msg.indexOf(MSG_BEGIN);
      if (startIndex === -1) {
        this.setState({ inputMsg: 'Missing a message begin marker.' });
      } else if (msg.indexOf(MSG_BEGIN, startIndex + 1) !== -1) {
        this.setState({ inputMsg: 'Duplicate message begin markers.' });
      } else {
        // Check for end marker
        const endIndex = msg.indexOf(MSG_END, startIndex);
        if (endIndex === -1) {
          this.setState({ inputMsg: 'Missing a message end marker.' });
        } else if (msg.indexOf(MSG_END, endIndex + 1) !== -1) {
          this.setState({ inputMsg: 'Duplicate message end markers.' });
        } else {
          // extract the actual message
          const encMsg = msg.substring(startIndex + MSG_BEGIN.length, endIndex);

          // Finally add the start/end bits
          let buf = msg.substring(0, startIndex);
          buf += decrypt_v3(encMsg, this.state.key);
          buf += msg.substring(endIndex + MSG_END.length);

          this.setState({ output: buf });
        }
      }
    }
  },

  handleEncrypt(e) {
    if (this.state.key.length === 0) {
      this.setState({ keyInvalid: true });
    } else if (this.state.key !== this.state.confirm) {
      this.setState({ confirmInvalid: true });
    } else {
      // Create the contents including the date/time.
      const contents = '*** Encrypted on '
        + (new Date()).toUTCString()
        + ' ***\n'
        + this.state.input
        + '\n*** End message ***';

      this.setState({ output: MSG_BEGIN + encrypt_v3(contents, this.state.key) + MSG_END });
    }
  },

  render() {
    const keyValidationState = this.state.keyInvalid ? "error" : null;
    const confirmValidationState = this.state.confirmInvalid ? "error" : null;
    const inputValidationState = this.state.inputMsg ? "error" : null;

    return (
      <Form horizontal>
        <FormGroup controlId="formHorizontalKey" validationState={keyValidationState}>
          <Col componentClass={ControlLabel} sm={1}>
            Key
          </Col>
          <Col sm={11}>
            <FormControl type="password"
              placeholder="Key"
              value={this.state.key}
              onChange={this.handleKeyChange} />
            {this.state.keyInvalid ? <HelpBlock>Key is missing</HelpBlock> : null}
          </Col>
        </FormGroup>

        <FormGroup controlId="formHorizontalConfirm" validationState={confirmValidationState}>
          <Col componentClass={ControlLabel} sm={1}>
            Confirm
          </Col>
          <Col sm={11}>
            <FormControl type="password"
              placeholder="Confirm (if encrypting)"
              value={this.state.confirm}
              onChange={this.handleConfirmChange} />
            {this.state.confirmInvalid ? <HelpBlock>Does not match Key</HelpBlock> : null}
          </Col>
        </FormGroup>

        <FormGroup>
          <Col smOffset={1} sm={10}>
            <ButtonToolbar>
              <Button onClick={this.handleDecrypt}>Decrypt</Button>
              <Button onClick={this.handleEncrypt}>Encrypt</Button>
              <Button onClick={this.handleClearKeys}>Clear Keys</Button>
              <Button onClick={this.handleClearAll}>Clear All</Button>
            </ButtonToolbar>
          </Col>
          <Col sm={1}>
            <CountdownTimer initialTimeRemaining={this.state.timeout}
              completeCallback={this.handleClearAll} />
          </Col>
        </FormGroup>

        <FormGroup controlId="inputTextarea" validationState={inputValidationState}>
          <Col componentClass={ControlLabel} sm={1}>
            Input
          </Col>
          <Col sm={11}>
            <FormControl componentClass="textarea" placeholder="Input" rows="5"
              value={this.state.input} onChange={this.handleInputChange} />
            {this.state.inputMsg ? <HelpBlock>{this.state.inputMsg}</HelpBlock> : null}
          </Col>
        </FormGroup>

        <FormGroup>
          <Col smOffset={1} sm={11}>
            <ButtonToolbar>
              <Button onClick={this.handleCopyUp}>Copy Up</Button>
            </ButtonToolbar>
          </Col>
        </FormGroup>

        <FormGroup controlId="outputTextarea">
          <Col componentClass={ControlLabel} sm={1}>
            Output
          </Col>
          <Col sm={11}>
            <FormControl componentClass="textarea" placeholder="Output" rows="5"
              value={this.state.output} onChange={this.handleOutputChange} />
          </Col>
        </FormGroup>
      </Form>
    );
  }
});

class App extends Component {
  render() {
    return (
      <div>
        <Grid>
          <Row>
            <Col>
              <h1>Hootie</h1>
            </Col>
          </Row>
        </Grid>
        <Jumbotron>
          <Grid>
            <HootieForm />
          </Grid>
        </Jumbotron>
      </div>
    );
  }
}

export default App;



